define('ember-youtube/components/ember-youtube', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	/*global YT*/
	var computed = Ember['default'].computed;
	var debug = Ember['default'].debug;
	var observer = Ember['default'].observer;
	var on = Ember['default'].on;
	var run = Ember['default'].run;

	exports['default'] = Ember['default'].Component.extend({
		classNames: ['EmberYoutube'],
		ytid: null,
		player: null,
		playerState: 'loading',
		isMuted: false,
		showControls: false,
		showTime: false,
		showProgress: false,
		showDebug: false,
		autoplay: 0,

		// from YT.PlayerState
		stateNames: {
			'-1': 'ready', // READY
			0: 'ended', // YT.Player.ENDED
			1: 'playing', // YT.PlayerState.PLAYING
			2: 'paused', // YT.PlayerState.PAUSED
			3: 'buffering', // YT.PlayerState.BUFFERING
			5: 'queued' // YT.PlayerState.CUED
		},

		// YouTube's embedded player can take a number of optional parameters.
		// Full list here: https://developers.google.com/youtube/player_parameters#Parameters
		// demo: https://developers.google.com/youtube/youtube_player_demo
		playerVars: {
			autoplay: 0,
			controls: 1,
			enablejsapi: 1,
			rel: 0, // disable related videos
			showinfo: 0,
			autohide: 1,
			fs: 0, // disable fullscreen button
			playsinline: 1
			// disablekb: 1,
			// iv_load_policy: 3,
			// modestbranding: 1,
		},

		// Make the component available to the outside world
		_register: on('init', function () {
			run.schedule('afterRender', this, function () {
				if (this.get('delegate')) {
					if (this.get('showDebug')) {
						debug('delegating');
					}
					if (this.get('showDebug')) {
						debug(this.get('delegate'));
					}
					if (this.get('showDebug')) {
						debug(this);
					}
					this.get('delegate').set(this.get('delegate-as') || 'emberYoutubePlayer', this);
				}
			});
		}),

		// update autoplay from true/false to 1/0 which yt api needs
		setAutoplay: on('init', Ember['default'].observer('autoplay', function () {
			this.playerVars.autoplay = this.get('autoplay') ? 1 : 0;
		})),

		// Load the iframe player API asynchronously from YouTube
		loadApi: on('init', function () {
			var tag = document.createElement('script');
			var firstTag = document.getElementsByTagName('script')[0];

			tag.src = 'https://www.youtube.com/iframe_api';
			firstTag.parentNode.insertBefore(tag, firstTag);

			// YouTube callback when API is ready
			window.onYouTubePlayerAPIReady = (function () {
				if (this.get('showDebug')) {
					debug('yt player api ready');
				}
				this.createPlayer();
			}).bind(this);
		}),

		isPlaying: computed('playerState', function () {
			var player = this.get('player');
			if (!player || this.get('playerState') === 'loading') {
				return false;
			}

			return player.getPlayerState() === YT.PlayerState.PLAYING;
		}),

		createPlayer: function createPlayer() {
			var _this = this;
			var playerVars = this.get('playerVars');
			var $iframe = this.$('#EmberYoutube-player');

			var player = new YT.Player($iframe[0], {
				width: 360,
				height: 270,
				playerVars: playerVars,
				events: {
					'onReady': _this.onPlayerReady.bind(_this),
					'onStateChange': _this.onPlayerStateChange.bind(_this),
					'onError': _this.onPlayerError.bind(_this)
				}
			});

			this.set('player', player);
			window.emberYouTubePlayer = player; // to access it outside ember
		},

		// called by the YouTube API
		onPlayerReady: function onPlayerReady() {
			this.set('playerState', 'ready');
			this.loadVideo();
		},

		// Load (and plays) a video every time ytid changes
		loadVideo: Ember['default'].observer('ytid', function () {
			var id = this.get('ytid');
			var player = this.get('player');

			// make sure we have access to the functions we need
			// otherwise the player might die
			if (!id || !player.loadVideoById || !player.cueVideoById) {
				if (this.get('showDebug')) {
					debug('no id');
				}
				return;
			}

			if (this.playerVars.autoplay) {
				player.loadVideoById(id);
			} else {
				player.cueVideoById(id);
			}
		}),

		// @todo: here for later
		// onVolumeChange: Ember.observer('volumeChange', function() {
		// 	if (this.get('volume')) {
		// 		this.send('play');
		// 	} else {
		// 		this.send('pause');
		// 	}
		// }),

		// called by YouTube
		onPlayerStateChange: function onPlayerStateChange(event) {
			// Get a readable state name
			var state = this.get('stateNames.' + event.data.toString());
			this.set('playerState', state);

			if (this.get('showDebug')) {
				debug(state);
			}

			// send actions outside
			this.sendAction(state);

			// send actions inside
			this.send(state);
		},

		// called by the API
		onPlayerError: function onPlayerError(event) {
			var errorCode = event.data;
			this.set('playerState', 'error');

			Ember['default'].warn('error' + errorCode);

			// Send the event to the controller
			this.sendAction('error', errorCode);

			// switch(errorCode) {
			// 	case 2:
			// 		Ember.warn('Invalid parameter');
			// 		break;
			// 	case 100:
			// 		Ember.warn('Not found/private');
			// 		this.send('playNext');
			// 		break;
			// 	case 101:
			// 	case 150:
			// 		Ember.warn('Embed not allowed');
			// 		this.send('playNext');
			// 		break;
			// 	default:
			// 		break;
			// }
		},

		startTimer: function startTimer() {
			var player = this.get('player');

			this.set('currentTime', player.getCurrentTime());
			this.set('duration', player.getDuration());

			// every 60ms, update current time
			var timer = window.setInterval((function () {
				this.set('currentTime', player.getCurrentTime());
			}).bind(this), 60);

			this.set('timer', timer);
		},

		stopTimer: function stopTimer() {
			window.clearInterval(this.get('timer'));
		},

		// avoids 'undefined' value for the <progress> element
		currentTimeValue: computed('currentTime', function () {
			var value = this.get('currentTime');
			return value ? value : 0;
		}),

		// returns a 0:00 format
		currentTimeFormatted: computed('currentTime', function () {
			var time = this.get('currentTime');
			if (!time) {
				return;
			}
			var minutes = Math.floor(time / 60);
			var seconds = Math.floor(time - minutes * 60);
			if (seconds < 10) {
				seconds = '0' + seconds;
			}
			return minutes + ':' + seconds;
		}),

		// avoids 'undefined' value for the <progress> element
		durationValue: computed('duration', function () {
			var value = this.get('duration');
			return value ? value : 0;
		}),

		// returns a 0:00 format
		durationFormatted: computed('duration', function () {
			var time = this.get('duration');
			if (!time) {
				return;
			}
			var minutes = Math.floor(time / 60);
			var seconds = time - minutes * 60;
			return minutes + ':' + seconds;
		}),

		// OK, this is really stupid but couldn't access the "event" inside
		// an ember action so here do a manual click handler instead
		progressBarClick: on('didInsertElement', function () {
			var self = this;

			this.$().on('click', 'progress', function (event) {

				// get the x position of the click inside our progress el
				var x = event.pageX - Ember['default'].$(this).position().left;

				// convert it to a value relative to the duration (max)
				var clickedValue = x * this.max / this.offsetWidth;

				// 250 = 0.25 seconds into player
				self.send('seekTo', clickedValue);
			});
		}),

		actions: {
			load: function load() {
				this.get('player').loadVideo();
			},
			play: function play() {
				this.get('player').playVideo();
			},
			pause: function pause() {
				this.get('player').pauseVideo();
			},
			mute: function mute() {
				this.get('player').mute();
			},
			unMute: function unMute() {
				this.get('player').unMute();
			},
			togglePlay: function togglePlay() {
				if (this.get('isPlaying')) {
					this.send('pause');
				} else {
					this.send('play');
				}
			},
			toggleVolume: function toggleVolume() {
				var player = this.get('player');
				this.toggleProperty('isMuted');
				if (player.isMuted()) {
					this.send('unMute');
				} else {
					this.send('mute');
				}
			},
			seekTo: function seekTo(ms) {
				this.get('player').seekTo(ms);
			},

			// youtube events
			ready: function ready() {},
			ended: function ended() {},
			playing: function playing() {
				this.startTimer();
			},
			paused: function paused() {
				this.stopTimer();
			},
			buffering: function buffering() {},
			queued: function queued() {} }
	});

});